<template>
  <div id="segment">
    <a class="anchor" id="segment-anchor"></a>
    <div class="component-header-wrapper">
      <h1 class="title is-1 has-text-centered is-marginless">Segments</h1>
      <p class="subtitle is-3 has-text-centered is-marginless">10 senaste</p>
      <div class="dropdown is-right" :class="{'is-active': openDropdown}" @click="openDropdown = !openDropdown">
        <div class="dropdown-trigger">
          <button class="button is-info is-medium" aria-haspopup="true" aria-controls="dropdown-menu6">
            <span>{{ currentSegment }}</span>
            <span class="icon is-small">
              <i class="fa fa-angle-down" aria-hidden="true"></i>
            </span>
          </button>
        </div>
        <div class="dropdown-menu" id="dropdown-menu6" role="menu">
          <div class="dropdown-content">
            <a class="dropdown-item" @click="currentSegment = 'Lilla Varvet'">
              Lilla varvet
            </a>
            <a class="dropdown-item" @click="currentSegment = 'Stora Varvet'">
              Stora varvet
            </a>
          </div>
        </div>
      </div>
    </div>
    <table class="table table-striped is-fullwidth">
      <thead>
        <tr>
          <th>kare</th>
          <th>Segment</th>
          <th>Distans</th>
          <th>Senaste varvtid</th>
          <th>Hastighet</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="data in segmentsCompleted" @click="openModal = !openModal">
          <td>{{ data.segmentCompleted.startRead.givenName + ' ' + data.segmentCompleted.startRead.familyName }}</td>
          <td>{{ data.segment.name }}</td>
          <td>{{ data.segmentCompleted.distance + ' m' }}</td>
          <td>{{ data.segmentCompleted.duration }}</td>
          <td>{{ data.segmentCompleted.speedKmPHour + ' km/h' }}</td>
          <td>{{ data.segmentCompleted.speedMinPerKm + ' min/km' }}</td>
        </tr>
      </tbody>
    </table>
    <transition name="expand">
      <div class="modal" :class="{'is-active': openModal}" v-if="openModal">
        <div class="modal-background"></div>
        <div class="modal-content">
          <iframe class="modal-video" allowfullscreen frameborder="0" src="https://www.youtube.com/embed/XMH_xG6U5u4?autoplay=1"></iframe>
        </div>
        <button class="modal-close is-large" aria-label="close" @click="openModal = !openModal"></button>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  name: "segment",
  data: () => ({
    openModal: false,
    currentSegment: 'Lilla Varvet',
    openDropdown: false
  }),
  props: ['segment', 'segmentsCompleted']
}
</script>
<style lang="scss" scoped>
#segment {
  overflow: auto;
  max-height: 100%;
  min-height: 100%;
  
  .component-header-wrapper {
    width: 100%;
    position: relative; 
    
    .dropdown {
      position: absolute;
      right: 1.5rem;
      bottom: 0.5rem;
      
      .dropdown-item {
        font-size: 1.4rem;
      }
    }
  }
  
  .modal {
    z-index: 999;
    
    .modal-content {
      background-color: #fff;
      
      .modal-video {
        width: 640px;
        height: 315px;
        display: block;
      }
    }
  }
  
  .table {
    
    tbody {
      tr {
        cursor: pointer;
      }
    }
  }
}

/* Animations
 *************/

</style>


