<template>
  <div id="header">
    <ul>
      <li :class="{active: currentRoute === '#map-anchor'}"><a href="#map-anchor" @click="setActiveRoute">Map</a></li>
      <li :class="{active: currentRoute === '#segment-counter-anchor'}"><a href="#segment-counter-anchor" @click="setActiveRoute">Passager</a></li>
      <li :class="{active: currentRoute === '#segment-anchor'}"><a href="#segment-anchor" @click="setActiveRoute">Segments</a></li>
      <li :class="{active: currentRoute === '#weather-anchor'}"><a href="#weather-anchor" @click="setActiveRoute">Vder</a></li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "header",
  data: () => ({
    currentRoute: ''
  }),
  methods: {
    setActiveRoute() {
      
      setTimeout(() => {
        this.currentRoute = window.location.hash;
        console.log(this.currentRoute);
      }, 0);
    }
  }
}
</script>
<style lang="scss" scoped>
  #header {
    background-color: rgb(9, 72, 85);
    position: fixed;
    height: 4.0rem;
    line-height: 4.0rem;
    z-index: 100;
    width: 100%;
    top: 0;
  }
  
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center;
    
    li {
      display: inline-block;
      height: 4.0rem;
      min-width: 8.0rem;;
      text-align: center;
      position: relative;
      font-size: 1.8rem;
      font-weight: bold;
      margin: 0 1.5rem;
      
      &.active {
        border-bottom: 3px solid white;
      
        a {
          color: white;
        }
      }
      
      &:hover {
        border-bottom: 3px solid white;
      }
      
      a {
        text-decoration: none;
        display: block;
        color: rgb(208, 195, 201);
      }
    }
  }
</style>


