<template>
  <div id="app" v-resize="onResize">
    <transition name="slide" mode="out-in">
        <app-header v-if="showHeader"></app-header>  
    </transition>
    
    <div class="div-group" :style="{height: windowSizes.top.height + unit}">
      <div id="div1" class="square top-left" :style="{width: windowSizes.top.leftWidth + unit}">
        <div class="resizable-border-vertical" @mousedown="initDrag('top', $event)"></div>
        <app-map :stations="stations" :stationRead="stationRead"></app-map>
      </div>
      <div id="div2" class="square top-right" :style="{width: windowSizes.top.rightWidth + unit}">
          <app-weather></app-weather>
      </div>
      <div class="resizable-border-horizontal" @mousedown="initDrag('horizontal', $event)"></div>
    </div>
    
    <div class="div-group" :style="{height: windowSizes.bottom.height + unit}">
      <div id="div3" class="square bottom-left" :style="{width: windowSizes.bottom.leftWidth + unit}">
        <div class="resizable-border-vertical" @mousedown="initDrag('bottom', $event)"></div>
        <app-segment :segmentsCompleted="segmentsCompleted"></app-segment>
      </div>
      <div id="div4" class="square bottom-right" :style="{width: windowSizes.bottom.rightWidth + unit}">
        <app-segment-counter></app-segment-counter>
      </div>  
    </div>
  </div>
</template>

<script>
import resize from 'vue-resize-directive'
import Map from './components/Map.vue'
import SegmentCounter from './components/SegmentCounter.vue'
import Segment from './components/Segment.vue'
import Weather from './components/Weather.vue'
import Header from './components/Header.vue'
import { MergedSegment } from './models/merged-segment.model.js';

export default {
  name: 'app',
  components: {
    'app-map': Map,
    'app-segment-counter': SegmentCounter,
    'app-segment': Segment,
    'app-weather': Weather,
    'app-header': Header
  },
  localStorage: {
    
  },
  data () {
    return {
      windowSizes: {
        top: {
          leftWidth: 50,
          rightWidth: 50,
          height: 50
        },
        bottom: {
          leftWidth: 50,
          rightWidth: 50,
          height: 50
        }
      },
      unit: '%',
      startWidth: 0,
      startHeight: 0,
      startX: 0,
      startY: 0,
      pos: '',
      showHeader: true,
      segmentsCompleted: [],
      segments: {},
      stations: [],
      stationRead: {}
    }
  },
  methods: {
    initDrag(pos, e) {
      this.pos = pos;
      this.startX = e.clientX;
      this.startY = e.clientY;
      this.startWidth = e.target.parentElement.clientWidth;
      this.startHeight = e.target.parentElement.style.height;
      document.addEventListener('mousemove', this.doDrag, false);
      document.addEventListener('mouseup', this.stopDrag, false);
    },
    doDrag(e) {
      var windowWidth = window.innerWidth;
      var windowHeight = window.innerHeight;
      
      var newLeftWidth = (e.x / windowWidth) * 100;
      var newRightWidth = 100 - newLeftWidth;
      
      var newTopHeight = (e.y / windowHeight) * 100;
      var newBottomHeight = 100 - newTopHeight;
      
      if (this.pos === 'top') {
        this.windowSizes.top.leftWidth = newLeftWidth;
        this.windowSizes.top.rightWidth = newRightWidth;
      } else if (this.pos === 'bottom') {
        this.windowSizes.bottom.leftWidth = newLeftWidth;
        this.windowSizes.bottom.rightWidth = newRightWidth;
      } else if (this.pos === 'horizontal') {
        this.windowSizes.top.height = newTopHeight;
        this.windowSizes.bottom.height = newBottomHeight;
      }
      this.$ls.set('windowSizes', JSON.stringify(this.windowSizes));
    },
    stopDrag(e) {
      document.removeEventListener('mousemove', this.doDrag, false)
      document.removeEventListener('mouseup', this.stopDrag, false)
    },
    onResize(el) {
      var appWidth = el.offsetWidth;
      
      if (appWidth > 1600) {
        this.showHeader = false;
      } else {
        this.showHeader = true;
      }
    },
    initSignalR() {
      var connection = $.hubConnection();
      var hub = connection.createHubProxy("statisticsHub");
      connection.url = 'http://ostersundsporttracker.azurewebsites.net/signalr';
      var vm = this;
      hub.on("tagRead", function(tagRead) {
        console.log("Tag read", tagRead);
        var readerName = tagRead.readerName;
        for (var i = 0; i < vm.stations.length; i++) {
          if (vm.stations[i].stationName === readerName) {
            var index = i;
            $('#station-' + vm.stations[index].id).addClass('pulse');
            setTimeout(() => {
              $('#station-' + vm.stations[index].id).removeClass('pulse');
            }, 500, index);
          }
        }
      });
      
      hub.on("segmentCompleted", function(segmentCompleted, segment) {
        var mergedSegment = new MergedSegment(segment, segmentCompleted);
        console.log("Merged segment", mergedSegment);
        if (vm.segmentsCompleted.length < 10) {
          vm.segmentsCompleted.unshift(mergedSegment);
        } else {
          vm.segmentsCompleted.pop(vm.segmentsCompleted.length);
          vm.segmentsCompleted.unshift(mergedSegment);
        }
        console.log("segments completed", vm.segmentsCompleted);
      });
      
      connection.start()
        .done(() => {
          console.log("Now connected, connection ID=" + connection.id);
        })
        .fail(() => {
          console.log("Could not connect");
        });
    },
    fetchStations() {
      return this.$http.get('Stations')
        .then(response => {
          return response.json();
        })
        .then(data => {
          this.stations = data;
        })
    }
  },
  directives: {
    resize
  },
  created() {
    var defaultSizes = JSON.stringify({
      top: {
        leftWidth: 50,
        rightWidth: 50,
        height: 50
      },
      bottom: {
        leftWidth: 50,
        rightWidth: 50,
        height: 50
      }
    });
    this.windowSizes = JSON.parse(this.$ls.get('windowSizes', defaultSizes));
    
    this.fetchStations()
      .then(() => {
        this.initSignalR();
      });
    
    
  },
  mounted() {
    var windowWidth = window.innerWidth;
    if (windowWidth > 1600) {
      this.showHeader = false;
    }
    
    this.segmentsCompleted.push(
      {
        segment: {
          name: 'Lilla Varvet'
        },
        segmentCompleted: {
          startRead: {
            givenName: 'Frnamn', 
            familyName: 'Efternamn'
          }, 
          distance: '200', 
          duration: '2', 
          speedKmPHour: '10',
          speedMinPerKm: '6'
        }
      }
    );
  }
}
</script>

<style lang="scss">
html,
body {
  height: 100%;
  width: 100%;
  font-size: 62.5%;
}

body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 0;
  margin: 0;
  font-weight: normal;
  font-size: 1.4rem;
}

* {
  box-sizing: border-box;
}

#app {
  height: 100%;
  width: 100%;
  
  .square {
    // background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  }
}

.anchor {
  display: block;
  position: relative;
  visibility: hidden;
  top: -40px;
}

.title {
  padding: 1rem 0;
  background-color: rgb(9,66,143);
  color: #fff;
  margin: 0;
}

.subtitle {
  background-color: rgb(9,66,143);
  color: #fff;
  padding-bottom: 0.5rem;
}

@media (max-width: 1299px) {
  
  #app {
    padding-top: 40px;
    transition: padding-top 0.5s ease-out;
  }
  
  .div-group {
    width: 100%;
    height: 200% !important;
  }

  .square {
    width: 100% !important;
    height: 50% !important;
  }
}

@media (min-width: 1300px) and (max-width: 1599px) {

  #app {
    padding-top: 40px;
    transition: padding-top 0.5s ease-out;
  }
  
  .div-group {
    width: 100%;
    height: 100% !important;
    overflow-x: hidden;
  }
  
  .square {
    float: left;
    position: relative;
    display: inline-block;
    width:100%;
    height: 100%;
  }
  
  .resizable-border-vertical {
    width: 4px;
    height: 100%;
    background-color: rgba(#585858, 0.7);
    position: absolute;
    right: -2px;
    z-index: 99;
    cursor: ew-resize;
  }

}

@media screen and (min-width: 1600px) {
  
  .div-group {
    width: 100%;
    height: 50%;
    position: relative;
    overflow: hidden;
  }
  
  .square {
    float: left;
    position: relative;
    display: inline-block;
    width: 50%;
    height: 100%;
  }
  
  .resizable-border-horizontal {
    height: 6px;
    width: 100%;
    z-index: 99;
    cursor: ns-resize;
    position: absolute;
    bottom: -3px;
    background-color: rgba(#585858, 0.7);
  }
  
  .resizable-border-vertical {
      width: 4px;
      height: 100%;
      background-color: rgba(#585858, 0.7);
      position: absolute;
      right: -2px;
      z-index: 99;
      cursor: ew-resize;
  }
}

.slide-enter {
  opacity: 0;
}

.slide-enter-active {
  animation: slide-in 0.5s ease-out forwards;
  position: absolute;
  transition: opacity .5s;
}

.slide-leave-active {
  animation: slide-out 1s ease-out forwards;
  transition: opacity .5s;
  opacity: 0;
  position: absolute;
}

.slide-move {
  transition: transform 1s;
}

@keyframes slide-in {
  from {
    transform: translateY(-40px);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes slide-out {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-40px);
  }
}

</style>



